/*edit-details*/
.edit-details-section {
    width: 55%;
    margin: 0 auto;
    margin-top: 200px;
    margin-bottom: 50px;
    background-color: #202020;
    padding: 60px;
    border-radius: 20px;
    box-shadow: 0px 0px 10px #fff;
}
.light-mode .edit-details-section{
    box-shadow: 0px 2px 10px #000;  
    border: 1px solid #000;
    background-color: #e4e4e4;
}
.edit-details-section .choose-pic {
    width: 25%;
    margin: 0 auto;
    text-align: center;
}
.edit-details-section textarea{
    padding: 10px 20px !important;
    font-size: 14px;
}
.profile-pic-outer{
    position: relative;
    border-radius: 50%;
    overflow: hidden;
    width: 240px;
    height: 240px;
    margin: 0 auto;
}
.profilePhoto {
    width: 100%;
    height: 100% !important;
}
.remove-btn {
    position: absolute;
    bottom: 0;
    width: 100%;
}
.remove-btn{
    display: none !important;
}
.profile-pic-outer:hover .remove-btn{
    display: block !important;
}
.edit-details-section .profile-pic-section{
    border: 3px solid #ea06b1;
    border-radius: 50%;
    overflow: hidden;
    width: 240px;
    height: 240px;
    margin: 0 auto;
}
.edit-details-section input#chooseImage {
    min-height: 60px;
    /* background-color: #1ae0e0 !important; */
    color: #a9e418 !important;
}
.edit-details-section #imageFileUpload{
    min-height: 40px;
    padding: 10px 10px !important;
    font-size: 14px !important;
}
.edit-details-section input#formBasicName {
    min-height: 50px;
    padding: 10px 10px !important;
    font-size: 14px !important;
}
.edit-details-section input#formBasicEmail {
    min-height: 50px;
    padding: 10px 10px !important;
    font-size: 14px !important;
    opacity: 0.5;
    cursor: not-allowed;
}
.edit-details-section input#formBasicAddress {
    min-height: 50px;
    padding: 10px 10px !important;
    font-size: 14px !important;
    opacity: 0.5;
    cursor: not-allowed;
}
.edit-details-section .label-form {
    width: 180px;
}
.edit-details-section .edit-form-section {
    width: 100%;
    margin: 0 auto;
}

/*privacy policy*/
.privacy-policy {
    width: 46%;
    margin: 0 auto;
    margin-top: 15%;
    margin-bottom: 100px;
    text-align: center;
    display: flex;
    flex-direction: column;
    gap: 40px;
}
.privacy-policy .purple{
    color:  #ea06b1;
}
.privacy-policy .yellow{
    color:  #a9e418;
}
.privacy-policy .privacy-head{
    margin-bottom: 50px;
    font-size: 30px;
}
.privacy-policy .privacy-head span {
    font-size: 40px;
}
.privacy-policy .google-policy{
    list-style: none;
    padding: 0;
}

/*connect in header*/
.head-button .connect-icon{
    display: none;
}
.head-button .connect-span{
    display: block;
}


/*header*/
#dropdown-basic:hover > span{
    color: #fff !important;
}
#dropdown-basic:active > span{
    color: #fff !important;
}
#dropdown-basic:focus > span{
    color: #fff !important;
}

.light-mode #dropdown-basic:hover > span{
    color: #000 !important;
}
.light-mode #dropdown-basic:active > span{
    color: #000 !important;
}
.light-mode #dropdown-basic:focus > span{
    color: #000 !important;
}

/*home page design*/
.sec-bg-gradient .profile-image {
    width: 150px;
    height: 150px;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0px 2px 10px #fff;
    border-radius: 20px;
    border: 1px solid #fff;
}
.light-mode .sec-bg-gradient .profile-image {
    box-shadow: 0px 2px 10px #000;
    border: 1px solid #000;
}
.sec-bg-gradient .profile-image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.sec-bg-gradient .image {
    width: 100%;
    height: 530px;
    overflow: hidden;
    border-radius: 10px;
    box-shadow: 0px 2px 10px #fff;
    border-radius: 20px;
    border: 1px solid #fff;
}
.light-mode .sec-bg-gradient .image {
    box-shadow: 0px 2px 10px #000;
    border: 1px solid #000;
}
.sec-bg-gradient .image img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}


