.library-tabs-section .nav-tabs .nav-link.active {
    background-color: transparent;
    border-color: #8cc600 #dee2e6 #fff;
    color: #fff;
    border: 0px;
    border-bottom: 3px solid #8cc600;
}
.library-tabs-section .nav-tabs {
   border: 0px;
}
.library-tabs-section .nav-tabs .nav-link{
    font-size: 28px;
    color: #495057;
    padding-left: 0px;
    padding-right: 0px;
    margin: 0px 20px;
    border: 0px;
}
.library-tabs-section hr{color: white;}
.light-mode .library-tabs-section .nav-tabs .nav-link {
    color: #495057;
}
/* .light-mode .library-tabs-section .nav-tabs .nav-link.active {
    color: #101010;
} */
.light-mode .library-tabs-section hr{color: #000;}

.light-mode .library-tabs-section .nav-tabs .nav-link.active{
    color: #000;
    background-color: transparent;

}