.dropdown-popup{
    position: relative;
}
.dropdown-popup .dropdown-content {
    width: 100%;
    max-width: 520px;
    float: right;
    background: #101010;
    position: absolute;
    right: 20px;
    top: -60px;
    z-index: 100;
    border-radius: 25px;
    box-shadow: inset 0 0 10px #e4e4e4;
}
.light-mode .dropdown-popup .dropdown-content{
    box-shadow: 0px 2px 10px #000;  
    border: 1px solid #000;
    background: #e4e4e4;

}
.dropdown-popup .yellow{
    color: #a9e418;
}
.dropdown-popup .purple{
    color: #ea06b1;
}
.dropdown-popup span{
    font-size: 22px;
    line-height: 25px;
}
.dropdown-popup .purple-dark{
    font-size: 24px;
    text-transform: uppercase;
    color: #ea06b1;
}
.dropdown-popup .dollar-icon {
    font-size: 70px;
    color: #ea06b1;
}
.dropdown-popup .nft-icon {
    font-size: 70px;
    color: #a9e418;
}
.dropdown-popup .account-name{
    font-size: 22px;
    line-height: 25px;
}
.light-mode .dropdown-popup .account-name{
    color: #000;
}
.dropdown-popup .amount {
    font-size: 22px;
    white-space: nowrap;
    text-overflow: ellipsis;
    overflow: hidden;
    width: 266px;
    margin-left: 15px;
}
.light-mode .dropdown-popup .amount{
    color: #000;
}
.dropdown-popup .code{
    word-wrap: break-word;
    font-size: 13px;
}
.dropdown-popup .img-box {
    text-align: center;
    background: #202020;
    width: 100%;
    padding: 15px 20px;
    display: flex;
    flex-flow: column;
    justify-content: space-between;
    height: 160px;
    border-radius: 16px;
}
.light-mode .dropdown-popup .img-box{
    background: #e4e4e4;
    box-shadow: 0px 2px 10px #000;  
border: 1px solid #000;
}
.light-mode .dropdown-popup .img-box span{
    color: #000;
}
.dropdown-popup .imgs-row{
 gap: 10px;   
}
.dropdown-popup .user-icon img {
    width: 100px;
}
.dropdown-popup hr{
    border-color: #202020;
}
.dropdown-popup .img-box span{
    display: block;
    font-size: 16px;
}
.dropdown-popup .theme-buttons {
    background: #202020;
    padding: 5px;
    flex: 2;
    border-radius: 50px;
}
.light-mode .dropdown-popup .theme-buttons{
    background: #e4e4e4;
    box-shadow: 0px 2px 10px #000;  
    border: 1px solid #000;
}
.dropdown-popup .theme-row{
    align-items: center;
}
.dropdown-popup .theme-row h5{
    flex: 1;
    margin-bottom: 0px;
    font-family: Gotham;
    font-size: 20px;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: -0.16500000655651093px;
    text-align: left;
}
.light-mode .dropdown-popup .theme-row h5{
    color: #000;
} 
.dropdown-popup .btn-group {
width: 100%;
text-align: center;
gap: 2px;
}
.dropdown-popup .theme-buttons .btn-primary, .dropdown-popup .theme-buttons .btn-primary:focus {
    color: #fff;
    background-color: #202020;
    border-color: #52365a;
    width: 114px;
    border-radius: 25px;
}
.light-mode .dropdown-popup .theme-buttons .btn-primary, .light-mode .dropdown-popup .theme-buttons .btn-primary:focus{
    color: #000;
    background-color: #e4e4e4;
    border-color: #52365a;
}
.btn-group > .btn:nth-child(n+3), .btn-group > :not(.btn-check) + .btn, .btn-group > .btn-group:not(:first-child) > .btn {
    border-top-left-radius: 0 !important;
    border-bottom-left-radius: 0 !important;
}
.dropdown-popup .btn-check:checked+.btn-primary{
    background: linear-gradient( 90deg, #DE05BA, #a9e418);
    border-color: #52365a !important;
    border-width: 2px;
}
/* .btn.btn-primary:focus, .btn.btn-primary:active {
    background-color: #52365a !important;
    border-color: #52365a !important;
} */
/* .dropdown-popup .btn-group>.btn:not(:last-child):not(.dropdown-toggle) {
    border-top-right-radius: 25px;
    border-bottom-right-radius: 25px;
} */
.dropdown-popup .switch-account{
    font-family: Gotham;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: 19px;
    letter-spacing: -0.16500000655651093px;
    color: #fff;
    text-decoration: none;  
}
.dropdown-popup .switch-account:hover{
    color: #a9e418;
}
.light-mode .dropdown-popup .switch-account{
    color: #000;
}
.light-mode .dropdown-popup .switch-account:hover{
    color: #a9e418;
}
.dropdown-popup .border-top {
    border-top: 1px solid #202020!important;
}
@media (max-width: 767px) {

.dropdown-popup .img-box{
    height: 120px;
}
.dropdown-popup .theme-buttons .btn-primary{
    width: 90px;
}
.dropdown-popup .dropdown-content{
    right: 0;
}
}


@media (max-width: 480px){
    .dropdown-popup span {
        font-size: 15px;
       
      }
      .dropdown-menu[data-bs-popper] {
        left: -33px !important;
    }
}

@media (max-width: 319px){
    .dropdown-popup span {
        font-size: 12px;
       
      }

      .dropdown-popup .dollar-icon {
        font-size: 48px;
    }
    .dropdown-popup .nft-icon {
        font-size: 46px;
    }
}

