.btn.btn-theme {
  color: #fff;
  background-color: #202020;
  border-color: #202020;
  border-radius: 25px;
  padding: 6px 15px;
  transition: 0.3s ease-in-out;
  font-size: 13px;
  position: relative;
}
.btn.btn-theme:hover{
  color: #202020;
  background-color: #fff;
  border-color: #202020;
}
.btn.btn-theme.bg-gray:hover {
  background: linear-gradient(90deg, #9000f3, #de05ba) !important;
  color: #fff !important;
  border-color: #9000f3 !important;
}
.bg-gradient-theme {
  background: linear-gradient(90deg, #9000f3, #de05ba) !important;
  color: #fff !important;
  border-color: #9000f3 !important;
}
.bg-gradient-theme:hover{
  color: #fff !important;
  background: #202020 !important;
  border-color: #fff !important;
}
.bg-gradient-circle {
  color: #fff !important;
  background: #202020 !important;
  border-color: #fff !important;
}
.bg-gradient-circle:hover{
  background: linear-gradient(90deg, #9000f3, #de05ba) !important;
  color: #fff !important;
  border-color: #9000f3 !important;
}
.bg-gradient-green {
  background: linear-gradient(264.18deg, #1ae0e0 2.5%, #8cc600 100%);
  color: #fff;
  border-color: #1ae0e0 !important;
}
.btn.btn-theme.bg-gradient:hover,
.btn.btn-theme.bg-gradient-green:hover {
  color: #fff !important;
  background: #202020 !important;
  border-color: #fff !important;
}
.bg-gray {
  background-color: #4c4c4d !important;
  border-color: #4c4c4d !important;
}
.bg-darkgrey {
  background: #202020;
}
.light-mode .bg-darkgrey {
