.music-content{
    color: #fff;
    position: relative;
}
.music-content{
    color: #fff;
    position: relative;
    grid-gap: 20px;
    gap: 20px;
    margin: 10px 0px;

    background-color: #202020;
    border-radius: 20px;
    box-shadow: 0px 0px 10px #fff;
    border: 1px solid #fff;
    padding: 15px;
}
.light-mode .music-content {
    box-shadow: 0px 0px 10px #000;
    border: 1px solid #000;
    background-color: #e4e4e4;
  }

  .singer-discriptiona-tab {
    background-color: #202020;
    border-radius: 20px;
    box-shadow: 0px 0px 10px #fff;
    border: 1px solid #fff;
    padding: 20px;
    min-height: 200px;
}
.light-mode .singer-discriptiona-tab  {
    box-shadow: 0px 0px 10px #000;
    border: 1px solid #000;
    background-color: #e4e4e4;
  }

.music-details h3, .music-details span{
    font-size: 13px;
    margin-bottom: 0px;
}
.music-details h3{
    width:150px; 
    height: 20px;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis; 
    display: block;
    white-space: nowrap;
    position: relative; 
    text-decoration: none;
}
.music-details .music-title span{
    width:140px; 
    height: 30px;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis; 
    display: block;
    white-space: nowrap;
    position: relative; 
    text-decoration: none;
    font-size: 20px;
    cursor: pointer;
}
.music-details .music-title span:hover{
    background-image: linear-gradient(45deg, #8200fe 17%, #ea06b1 50%); 
    background-clip: text;
     -webkit-background-clip: text;
     font-size: 16px;
     -webkit-text-fill-color: transparent;
     font-size: 20px;
}
.music-details .music-title p{
    width:140px; 
    height: 20px;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis; 
    display: block;
    white-space: nowrap;
    position: relative; 
    text-decoration: none;
}
