.music-head{
    gap: 20px;
    align-items: center;
}
.bg-gradient{
    background: linear-gradient( 
      90deg, #9000F3, #DE05BA) !important;
    color: #fff !important;
    border-color: #9000F3 !important;
  }

  .music-list button.btn.btn-theme{
    width: 100%;
    max-width: 110px;
    height: 32px;
    justify-content: center;
    font-size: 13px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.like, .view {
    display: flex;
    gap: 5px;

}
.like svg, .view svg{
    margin-top: 4px;
    cursor: pointer;
}
.music {
cursor: pointer;
}

.artist-dashboard .music-list button.btn.btn-theme{
    width: auto;
    max-width: none;
    min-width: 110px;
}
/* .trending-section .slick-track{
    width: 2600px !important;
} */
.music-image{
    width: 90px;
    height: 90px;
    border-radius: 30px;
    overflow: hidden;
}
.music-image img {
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.music-details {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    width: calc( 100% - 110px );
}
.nft-slide-price{
     overflow: hidden;
     white-space: nowrap;
     text-overflow: ellipsis;
     width: 35px;
}



@media (max-width: 1399px){
    .songs-artist-button {
        width: 95px;
      }
      .music-head {
        grid-gap: 10px;
        gap: 10px;
      }
      .music-title {
        width: calc(100% - 105px);
      }
      .music-details .music-title span {
        width: 115px;
      }
      .music-details .music-title p {
        width: 115px;
      }
    
}

@media (max-width: 991px){
    .music-details .music-title p {
        width: 90px;
    }
    .music-details .music-title span {
        width: 90px;
    }
}

@media (max-width: 767px){
    .music-list button.btn.btn-theme {
        height: 27px;
        font-size: 10px;
        min-width: 100px !important;
    }

}



@media (max-width: 480px){
    .music-content {
        flex-direction: column;
        align-items: center;
    }
}

