header#header {
    display: block;
    color: #fff;
    padding-top: 30px;
    margin-bottom: 50px;
    background: transparent;
    position: absolute;
    top: 0;
    left: 00;
    z-index: 9;
    width: 100%;
}
.user-icon {
    cursor: pointer;
    width: 50px;
    height: 50px;
    border-radius: 25%;
    overflow: hidden;
}
.user-icon img{
    width: 100%;
    height: 100%;
    object-fit: cover;
}
.light-mode .user-icon {
    box-shadow: 0px 2px 10px #000;  
    border: 1px solid #000;
    
}
.banner-section header#header{
    margin-bottom: 20px;
}
.navbar-light .navbar-nav .nav-link, .navbar-light .navbar-nav .nav-link{
    padding: 5px 16px !important;
}
.navbar-light .navbar-nav .nav-link, .navbar-light .navbar-nav .nav-link:focus {
    color: #434343;
    font-size: 22px;
}
.light-mode .navbar-light .navbar-nav .nav-link,.light-mode .navbar-light .navbar-nav .nav-link:focus {
    color: #535151;
}

.navbar-light .navbar-nav .nav-link:hover, .navbar-light .navbar-nav .nav-link:active{
    color: #fff;
}
.navbar-light .navbar-nav li.nav-item.active .navlink{
    color: #fff !important;
}
li.nav-item.active .nav-link {
    color: #fff !important;
}
.light-mode .navbar-light .navbar-nav .nav-link:hover,.light-mode  .navbar-light .navbar-nav .nav-link:active{
    background-image: linear-gradient(45deg, #8200fe 17%, #ea06b1 50%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.light-mode .navbar-light .navbar-nav li.nav-item.active .nav-link{
    background-image: linear-gradient(45deg, #8200fe 17%, #ea06b1 50%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.light-mode .navbar-nav li.nav-item.active .nav-icon {
    color: #ea06b1;
}
.light-mode .navbar-nav li.nav-item:hover .nav-icon {
    color: #ea06b1;
}

.light-mode .dashboard-header .navbar-nav .nav-link:hover,.light-mode .dashboard-header .navbar-nav .nav-link:active{
    background-image: linear-gradient(45deg, #8cc600  30%, #1ae0e0 50%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.light-mode .dashboard-header .navbar-nav li.nav-item.active .nav-link{
    background-image: linear-gradient(45deg, #8cc600  30%, #1ae0e0 50%);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}
.light-mode .dashboard-header .navbar-nav li.nav-item.active .nav-icon {
    color: #8cc600;
}
.light-mode .dashboard-header .navbar-nav li.nav-item:hover .nav-icon {
   color: #8cc600;
}

.nav-icon {
    padding-right: 15px;
}
