    position: absolute;
    top: 12px;
    right: 18px;
    font-size: 18px;
}
.header-search .form-control{
    background: #202020;
    border: none;
    border-radius: 16px;
    border-top-right-radius: 16px !important;
    border-bottom-right-radius: 16px !important;
    height: 48px;
    color: #fff;
}

