.trending-slider-section .slick-slider {
    height: 190px;
    overflow: hidden;
}
.trending-slider-section .slick-slide {
    height: 95%;
      margin-top: 5px;
  }
.trending-slider-section .slick-track {
    height: 190px !important;
}

.most-nft-slider-section .slick-slider {
    height: 420px;
    overflow: hidden;
}
.most-nft-slider-section .slick-list {
    height: 100%;
}
.most-nft-slider-section .slick-track {
    padding-top: 5px;
}

.investors-slider-content .slick-slider {
    height: auto;
    overflow: hidden;
}
.investors-slider-content .slick-track{
    width: 100% !important;
}
.investors-slider-content .slick-slide {
    box-shadow: 0px 2px 10px #fff;
    border-radius: 20px;
    border: 1px solid #fff;
    margin: 10px 0px;
    width: 100% !important;
    padding: 30px;
}
.light-mode .investors-slider-content .slick-slide{
    box-shadow: 0px 2px 10px #000;border: 1px solid #000;
}



@media (max-width: 1399px){
    .slick-track {
        width: 1450px !important;
    }
    /* .slick-slide {
        margin: 5px 20px;
    } */
    .trending-slider-section .slick-slider {
        height: 164px;
        overflow: hidden;
    }
    
    .most-nft-slider-section .slick-slider {
        height: 416px;
        overflow: hidden;
    }
    .trending-slider-section .slick-slider  .music-content {
        margin: 2px 0px;
    }
}

