
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.single-wallet img {
  height: auto;
  margin-bottom: 50px;
  width: 30%;
}

.wallet-connect-area {
	height: 100vh;
	display: flex;
	justify-content: center;
	align-items: center;
  }
  .intro span{
	font-size: 30px;
	font-weight: 700;
  }
  .intro h3{
	color: #fff;
	/* opacity: 0.8; */
  }
  .light-mode .intro h3{
	  color: #000;
  }

.single-wallet {
  border: 2px darkgray;
  background-color: rgb(27, 27, 27);
  padding: 40px;
  border-radius: 10px;
  transition: all ease-in-out 0.3s;
  border: 1px solid #fff; border-radius: 20px; box-shadow: 0px 0px 10px #fff; 
  cursor: pointer;
}
.single-wallet:hover{
  -webkit-transform: translateY(-5px);
  transform: translateY(-5px);
  transition: all ease-in-out 0.3s;
}
.single-wallet:hover .meta-head {
   color: #a9e418 !important;
}
.wallet-connect-area .items{
  margin-top: 50px;
}
.item {
  display: contents;
  width: 100%;
}


.single-wallet-login{
  border: 2px darkgray;
  background-color: rgb(27, 27, 27);
  padding: 40px;
  border-radius: 10px;
  transition: all ease-in-out 0.3s;
  border: 1px solid #fff; border-radius: 20px; box-shadow: 0px 0px 10px #fff; 
  cursor: pointer;
}
.wallet-login-address{
  max-width: 540px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  margin: 0 auto;
}
.single-wallet-login:hover{
  -webkit-transform: translateY(-5px);
  transform: translateY(-5px);
  transition: all ease-in-out 0.3s;
}
.single-wallet-login:hover .address-head{
  color: #a9e418 !important;
}
.address-head {
  font-size: 26px;
  text-transform: uppercase;
  font-weight: 700;
}

.wallet-connect-area .btn-white {
    background: #f7f7f7;
    color: #000;
    border-radius: 96px;
    border: 1px solid #fff;
    height: 43px;
    font-weight: 700;
    padding: 10px 20px;
    text-decoration: none;
    font-size: 14px;
	width: 100%;
}

.model-gap p {
	max-width: 540px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  margin: 0 auto;
}


.wallet-connect-area .signup-form {
    max-width: 55%;
    margin: 0 auto;
    background-color: #202020;
    border-radius: 20px;
    padding: 30px 50px;
	border: 1px solid #fff; border-radius: 20px; box-shadow: 0px 0px 10px #fff; 
}
.wallet-connect-area .signup-form:hover .yellow{
	color: #a9e418 !important;
}
.wallet-connect-area .privacy-policy-tag{
	color: #DE05BA;
	text-decoration: none;
}
.wallet-connect-area .form-control {
    min-height: 50px;
    background: #282828 !important;
    border: none !important;
    color: #fff !important;
    font-size: 18px !important;
    padding: 20px 20px !important;
}

/*resposive*/
@media (max-width: 1600px){
  	.intro span {
    	font-size: 24px;
	}
	.intro p {
		font-size: 12px;
	}
	.wallet-connect-area .items {
		margin-top: 20px;
	}
	.single-wallet {
		padding: 20px 40px;
	}
	.single-wallet-login {
		padding: 20px 40px;
	}
	.single-wallet img {
		margin-bottom: 30px;
	}
}

@media (max-width: 1399px){
	.single-wallet img {
		margin-bottom: 25px;
		width: 25%;
	}
	.single-wallet p {
		font-size: 12px;
	}
	.single-wallet-login p{
		font-size: 12px;
	}
	.address-head {
		font-size: 22px;
	}
	/*signup*/
	.intro h3 {
		font-size: 20px;
	}
	.model-gap p {
		font-size: 12px;
		margin: 0;
	}
	.form-control {
		min-height: 35px;
		font-size: 16px !important;
		padding: 13px 20px !important;
	}
}

@media (max-width: 1199px){
	.wallet-connect-area .signup-form {
		max-width: 60%;
	}
}

@media (max-width: 991px){
	.wallet-connect-area .signup-form {
		max-width: 70%;
	}
}

@media (max-width: 767px){
	.wallet-connect-area .btn-white {
		height: 43px;
		font-size: 14px;
	}
	.address-head {
		font-size: 20px;
	}
	.wallet-connect-area .signup-form {
		max-width: 95%;
	}
}

@media (max-width: 575px){
	.wallet-connect-area .signup-form {
		padding: 30px 20px;
	}
	.wallet-connect-area .form-control {
		font-size: 16px !important;
	}
}

@media (max-width: 480px){
	.intro span {
		font-size: 22px;
	}
	.single-wallet {
		padding: 20px 10px;
	}
	.single-wallet-login {
		padding: 20px 10px;
	}
	.wallet-connect-area .btn-white {
		height: 43px;
		font-size: 12px;
	}
	.address-head {
		font-size: 18px;
	}
	.wallet-connect-area .form-control {
		font-size: 14px !important;
	}
}

