} */

.bulk-buy-popup .cart-details h4 {
    color: #ffffff;
}

.bulk-buy-popup .cart-details .option-desc {
    color: #ddddd0;
}  

.bulk-buy-popup .cart-details .option-price {
    color: #ffffff;
    display: flex;
    align-items: center;
}

.bulk-buy-popup .cart-details .option-desc.dim, .bulk-buy-popup .cart-details .option-price.dim {
    color: #4e4e4e;
}  

.bulk-buy-popup .modal-content {
    background: #101010;
    box-shadow: inset 0 0 10px #e4e4e4;
    color: #fff;
    border-radius: 25px;
    overflow: hidden;
}
.bulk-buy-popup .bulk-buy-content {
    padding: 20px;
    box-sizing: border-box;
}
.bulk-buy-popup .bulk-buy-content .sub-heading {
    color: #DE05BA !important;
}
.bulk-buy-popup .bulk-buy-content .cross-btn {
    background: #d705bf;
    color: #fff;
}
.bulk-buy-popup .price-from-artist{
    max-width: 135px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    display: inline-block;
    font-size: 24px;
}

/*light mode*/
.light-mode .bulk-buy-popup .modal-content{
    background: #e4e4e4;
    box-shadow: inset 0 0 10px #000;
    color: #000;
}
.light-mode .bulk-buy-popup .cart-details h4 {
    color: #000;
}
.light-mode .bulk-buy-popup .cart-details .option-desc {
    color: #4e4e4e;
}  



@media  (min-width: 768px){
    .bulk-buy-popup .modal-dialog {
        max-width: 700px;
        margin: 1.75rem auto;
    }
}



@media  (max-width: 767px){
    .bulk-buy-popup .cart-details .option-price {
        justify-content: center !important;
    }
    .bulk-buy-popup .cart-details .quantity {
        justify-content: center !important;
    }

}

@media  (max-width: 480px){
    .bulk-buy-popup .cart-details .option-price {
        margin-top: 10px;
    }
}

