.cart-popup .modal-content {
    background: #101010;
    box-shadow: inset 0 0 10px #e4e4e4;
    color: #fff;
    border-radius: 25px;
}
.light-mode .cart-popup .modal-content {
    background: #e4e4e4;
    box-shadow: inset 0 0 10px #000;
    color: #000;
}
.cart-popup .cross-btn{
background: #d705bf;
color: #fff;
}
.cart-content {
    padding: 20px;
    box-sizing: border-box;
}
.cart-popup .modal-dialog {
    max-width: 414px;
}
.cart-popup .cart-details span{
    font-size: 20px;
    align-items: center;
}
.cart-popup .cart-details .ethrum-rate {
    width: calc( 100% - 100px );
    justify-content: end;
}
.cart-popup .cart-details .price-pay-span {
    max-width: 140px;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
}
.cart-popup .cart-details .ethereum-icon:before {
    color: #fff;
    content: '';
    width: 25px;
    height: 20px;
    top: 0px;
}
.light-mode .cart-popup .cart-details .ethereum-icon:before {
    color: #000;
}
.cart-button {
    display: flex;
    align-items: center;
    margin: auto;
    margin-top: 40px;
    grid-gap: 10px;
    grid-gap: 10px;
    grid-gap: 10px;
    gap: 3px;
    border-color: #c203cf;
    height: 45px;
    color: #fff;
    border-radius: 30px;
    padding: 16px 25px;
}
.cart-button span{
    color: #fff;
}
/* button.btn.btn-theme.cart-button span{
    color: #bf03d0;
}
button.btn.btn-theme.cart-button:hover span {
    color: #bf03d0;
} */
button.btn.btn-theme.cart-button:hover{
    background-color: #202020;
}
.cart-popup .sub-heading{
    text-shadow: 0px 4px 4px rgb(0 0 0 / 25%);
}
.cart-content .sub-heading{
    color: #DE05BA !important;
}



@media (max-width: 480px){
/*pay participate*/
.cart-content {
    padding: 20px 0px;
  }
  .cart-content .participate-input {
    width: calc( 100% - 100px );
}
.cart-popup .cart-details span {
    font-size: 14px;
}
}


