.home-slider .slide-content {
    /* background-image: url('../../images/cards-music.svg'); */
    background-size: cover;
    background-repeat: no-repeat;
    height: 404px;
    border-radius: 18px;
    padding: 130px 0px 0px;
    color: #fff;
    background-position: center center;
    /* margin-bottom: 30px; */
    position: relative;
    /* margin-top: 120px; */
}
.light-mode .home-slider .slide-content{
    color: #000;
}
.home-slider .slide{
    padding: 0px 15px;
}
.slide-content .play-icon svg{
    color: #ffffff73;
    font-size: 50px;
}
.slide-head{
    position: relative;
}
.slide-content h3{
    font-size: 24px;
    color: #fff;
    line-height: 30px;
    text-transform: uppercase;
    margin-top: 10px;
    background-image: linear-gradient(45deg, #8200fe 10%, #ea06b1 50%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    cursor: pointer;
}
.dashboard-slide-content h3{
    background-image: linear-gradient(45deg, #8cc600  120%, #1ae0e0 50%) !important;
}
.slide-content h3:hover{
 transform: scale(1.2);
}
.light-mode .slide-content h3 {
    color: #000;
}
.client-details{
    width: 100%;
    color: #fff;
    margin-top: 0px;
}
.light-mode .client-details {
    color: #000;
}
.icons-row{
font-size: 13px;
gap: 20px;
margin-right: 8px;
}


.subtract {
    font-size: 13px;
    gap: 10px;
    display: flex;
    align-items: center;
    margin-left: 10px;
}
.slide-bottom .button-aligns {
    gap: 10px;
}
.slide-bottom {
    margin-top: 10px;
}
.client-details h5 {
    font-size: 16px;
}

